import React, { Component } from "react";

import billboardmp4 from "./hk_net_1080pr.mp4";
import billboardwebm from "./hk_net_1080pr.webm";

import { SiteTitle, BillboardVideo, MainWrapper } from "../components/Headings";

export default class Index extends Component {
  render() {
    return (
      <div>
        <BillboardVideo>
          <source src={billboardmp4} type="video/mp4" />
          <source src={billboardwebm} type="video/webm" />
        </BillboardVideo>
        <MainWrapper>
          <SiteTitle>I am Mike Polinowski</SiteTitle>
          <p>I build web applications.</p>
        </MainWrapper>
      </div>
    );
  }
}



